<template>
  <label>
    <div class="image-upload">
      <img :src="dataURI" width="100%" v-if="dataURI"/>
      <div class="upload" v-if="!dataURI">
        <i class="fa fa-arrow-circle-o-up" aria-hidden="true"></i>
        <p>upload</p>
      </div>
      <input type="file" accept="image/*" @change="onChange"/>
    </div>
  </label>
</template>

<script>
  import co from 'co'
  import oss from 'ali-oss'

  export default {
    props:["url"],
    data() {
      return {
        dataURI: false
      }
    },
    mounted() {
      this.dataURI = this.url;
    },
    watch: {
      url (val) {
        this.dataURI = val;
      }
    },
    methods: {
      onChange(e) {
        const _this = this;
        const files = e.target.files || e.dataTransfer.files;

        if (!files.length) {
          return;
        }

        const file = files[0];

        const reader = new FileReader();

        reader.onload = e => {
          this.dataURI = e.target.result;
        };

        // read blob url from file data
        reader.readAsDataURL(file);

        var client = new oss.Wrapper({
          endpoint: 'oss-cn-hongkong.aliyuncs.com',
          accessKeyId: 'LTAIdmpYJJABLNHe',
          accessKeySecret: 'Fxu4UuEBI5baDxM7rccz8a4NbxzEWL',
          bucket: 'debit',
          debug: false
        });
        var name = 'idcard_'+Math.random()+'.png';
        client.multipartUpload(name, file).then(function (result) {
          //
          var url = 'http://debit.oss-cn-hongkong.aliyuncs.com/' + name;
          _this.$emit('uploadImage',url);
        }).catch(function (err) {
          console.log(err);
        });
      }
    },
  }
</script>



