<template>
  <f7-page>
    <f7-navbar title="Working information" back-link="Back"></f7-navbar>

    <div class="mt-44">
      <f7-list no-hairlines-md>
        <!--<f7-list-item>
          <f7-label>Type of work</f7-label>
          <f7-input type="text" placeholder="Please enter" clear-button></f7-input>
        </f7-list-item>
        -->

        <li>
          <div class="item-content item-input">
            <div class="item-inner">
              <div class="item-title item-label">Type of work</div>
              <select name="mac-windows" v-model="model.typeOfWork">
                <option v-for="item in selection['work']" :value="item.selectValue">{{item.selectText}}</option>
              </select>
            </div>
          </div>
        </li>

        <li>
          <div class="item-content item-input">
            <div class="item-inner">
              <div class="item-title item-label">Monthly income</div>
              <select name="mac-windows" v-model="model.monthIncome">
                <option v-for="item in selection['monthincome']" :value="item.selectValue">{{item.selectText}}</option>
              </select>
            </div>
          </div>
        </li>

        <f7-list-item>
          <f7-label>Company name</f7-label>
          <f7-input type="text" placeholder="Please enter" @input="model.companyName = $event.target.value" v-model="model.companyName" clear-button></f7-input>
        </f7-list-item>

        <li>
          <div class="item-content item-input">
            <div class="item-inner">
              <div class="item-title item-label">Company province</div>
              <select name="mac-windows" v-model="model.companyProvince">
                <option v-for="item in selection['province']" :value="item.selectValue">{{item.selectText}}</option>
              </select>
            </div>
          </div>
        </li>

        <!--
        <li>
          <a class="item-link smart-select smart-select-init" data-open-in="sheet" data-routable-modals="false" data-close-on-select="true">
            <select name="mac-windows">
              <option value="">male</option>
            </select>
            <div class="item-content item-input">
              <div class="item-inner">
                <div class="item-title item-label">Company city</div>
              </div>
            </div>
          </a>
        </li>

        <li>
          <a class="item-link smart-select smart-select-init" data-open-in="sheet" data-routable-modals="false" data-close-on-select="true">
            <select name="mac-windows">
              <option value="">male</option>
            </select>
            <div class="item-content item-input">
              <div class="item-inner">
                <div class="item-title item-label">Company districs</div>
              </div>
            </div>
          </a>
        </li>
        -->

        <f7-list-item>
          <f7-label>address</f7-label>
          <f7-input type="text" placeholder="Please enter" @input="model.address = $event.target.value" v-model="model.address" clear-button></f7-input>
        </f7-list-item>

        <f7-list-item>
          <f7-label>Company phone number</f7-label>
          <f7-input type="text" placeholder="Please enter" @input="model.companyPhone = $event.target.value" v-model="model.companyPhone" clear-button></f7-input>
        </f7-list-item>
      </f7-list>

      <div class="sf" v-if="success || failed">
        <div class="success" v-if="success">
          <img src="../images/success.png" width="50">
          <p>Save success</p>
        </div>
        <div class="failed" v-if="failed">
          <img src="../images/failed.png" width="50">
          <p>Save success</p>
        </div>
      </div>
    </div>

    <f7-toolbar class="button-bottom">
      <a class="col button button-big button-fill" :class="{ disable: !save }" @click="submit">Save</a>
    </f7-toolbar>
  </f7-page>
</template>

<script>
  export default {
    data() {
      return {
        save: true,
        success: false,
        failed: false,
        selection: {},
        model: {
          monthIncome: '',
          companyName: '',
          companyProvince: '',
          companyPhone: '',
          address: '',
          typeOfWork: '',
          userId:localStorage.getItem('userId')
        }

      }
    },
    mounted() {
      this.load();
    },
    methods: {
      load() {
        const _this = this;
        this.$http.get('Main/GetSelection')
          .then(function (response) {
            var arr = {};
            var data = response.data.data;
            for (var i = 0; i < data.length; i++) {
              var value = data[i];
              if (!arr[value.selectType]) {
                arr[value.selectType] = [];
              }
              arr[value.selectType].push(value);
            }
            _this.selection = arr;
            _this.userInfo();
          })
          .catch(function (error) {
            _this.failed = true
          });
      },
      userInfo () {
        const _this = this;
        this.$http.get('User/GetUserAllInfo', {params: {userId:localStorage.getItem('userId')}})
          .then(function (response) {
            var data = response.data.data;
            _this.model = data.userWorkingInfo;
          })
          .catch(function (error) {
            _this.failed = true
          });
      },
      submit() {
        this.$f7.dialog.create({
          title: 'Save information',
          text: 'Whether to save the modified information?',
          buttons: [
            {text: 'Save', onClick: this.ajax},
            {text: 'Give up'}
          ],
          verticalButtons: true
        }).open();
      },
      async ajax() {
        this.loading = true;
        const _this = this;
        await this.$http.get('User/EditUserWorkingInfo', {
          params: _this.model
        })
          .then(async function (response) {
            _this.loading = false;
            //
            if(response.data.result != 0){
              _this.failed = true;
              _this.msg = response.data.message;
              await _this.sleep(1000);
              _this.failed = !_this.failed;
            } else {
              //
              _this.success = true;
              await _this.sleep(1000);
              _this.success = !_this.success;
            }
          })
          .catch(function (error) {
            _this.failed = true
          });
      },
      async sleep(ms) {
        return new Promise(resolve => setTimeout(resolve, ms))
      }
    },
  }
</script>



