<template>
  <f7-page>
    <f7-navbar title="Personal information" back-link="Back"></f7-navbar>

    <div class="mt-44 pb-13">
      <f7-list no-hairlines-md>
        <f7-list-item>
          <f7-label>Full name</f7-label>
          <f7-input type="text" :value="model.fullName" @input="model.fullName = $event.target.value" placeholder="Please enter" clear-button></f7-input>
        </f7-list-item>

        <f7-list-item>
          <f7-label>ID Card number</f7-label>
          <f7-input type="text" :value="model.idCard" @input="model.idCard = $event.target.value" placeholder="Please enter" clear-button></f7-input>
        </f7-list-item>

        <f7-list-item>
          <f7-label>Biological mother's name</f7-label>
          <f7-input type="text" :value="model.motherName" @input="model.motherName = $event.target.value" placeholder="Please enter" clear-button></f7-input>
        </f7-list-item>

        <li>
          <div class="item-content item-input">
            <div class="item-inner">
              <div class="item-title item-label">Gender</div>
              <select v-model="model.gender">
                <option v-for="item in selection.sex" :value="item.selectValue">{{item.selectText}}</option>
              </select>
            </div>
          </div>
        </li>

        <li>
          <div class="item-content item-input">
            <div class="item-inner">
              <div class="item-title item-label">Education</div>
              <select class="select-box" v-model="model.education">
                <option v-for="item in selection.education" :value="item.selectValue">{{item.selectText}}</option>
              </select>
            </div>
          </div>
        </li>

        <li>
          <div class="item-content item-input">
            <div class="item-inner">
              <div class="item-title item-label">Martial status</div>
              <select v-model="model.maritalStatus">
                <option v-for="item in selection.marital" :value="item.selectValue">{{item.selectText}}</option>
              </select>
            </div>
          </div>
        </li>

        <li>
          <div class="item-content item-input">
            <div class="item-inner">
              <div class="item-title item-label">Number of children</div>
              <select v-model="model.numberOfChildren">
                <option v-for="item in selection.children" :value="item.selectValue">{{item.selectText}}</option>
              </select>
            </div>
          </div>
        </li>

        <li>
          <div class="item-content item-input">
            <div class="item-inner">
              <div class="item-title item-label">Residential province</div>
              <select v-model="model.residentialProvince">
                <option v-for="item in selection.province" :value="item.selectValue">{{item.selectText}}</option>
              </select>
            </div>
          </div>
        </li>
<!--
        <li>
          <a class="item-link smart-select smart-select-init" data-open-in="sheet" data-routable-modals="false" data-close-on-select="true">
            <select name="mac-windows" :value="fullName" @input="fullName = $event.target.value">
              <option value="">male</option>
            </select>
            <div class="item-content item-input">
              <div class="item-inner">
                <div class="item-title item-label">Residential city</div>
              </div>
            </div>
          </a>
        </li>

        <li>
          <a class="item-link smart-select smart-select-init" data-open-in="sheet" data-routable-modals="false" data-close-on-select="true">
            <select name="mac-windows" :value="fullName" @input="fullName = $event.target.value">
              <option value="">male</option>
            </select>
            <div class="item-content item-input">
              <div class="item-inner">
                <div class="item-title item-label">Residential districs</div>
              </div>
            </div>
          </a>
        </li>
-->

        <f7-list-item>
          <f7-label>address</f7-label>
          <f7-input type="text" :value="model.address" @input="model.address = $event.target.value" placeholder="Please enter" clear-button></f7-input>
        </f7-list-item>

        <li>
          <div class="item-content item-input">
            <div class="item-inner">
              <div class="item-title item-label">Occupancy duration</div>
              <select v-model="model.occupancyDuration">
                <option v-for="item in selection.occupancyduration" :value="item.selectValue">{{item.selectText}}</option>
              </select>
            </div>
          </div>
        </li>

        <f7-list-item>
          <f7-label>facebook/instagram</f7-label>
          <f7-input type="text" :value="model.socialAccounts" @input="model.socialAccounts = $event.target.value" placeholder="Please enter" clear-button></f7-input>
        </f7-list-item>
      </f7-list>

      <div class="sf" v-if="success || failed">
        <div class="success" v-if="success">
          <img src="../images/success.png" width="50">
          <p>Save success</p>
        </div>
        <div class="failed" v-if="failed">
          <img src="../images/failed.png" width="50">
          <p>{{msg ? msg : 'Save failed'}}</p>
        </div>
      </div>
    </div>

    <f7-toolbar class="button-bottom">
      <a class="col button button-big button-fill" :class="{ disable: !save }" @click="submit">Save</a>
    </f7-toolbar>

    <div class="sf" v-if="success || failed">
      <div class="success" v-if="success">
        <img src="../images/success.png" width="50">
        <p>Save success</p>
      </div>
      <div class="failed" v-if="failed">
        <img src="../images/failed.png" width="50">
        <p>Save failed</p>
      </div>
    </div>
  </f7-page>
</template>

<script>
export default {
  data () {
    return {
      save: true,
      success: false,
      failed: false,
      msg: '',
      selection:{},
      model: {
        fullName: '',//??
        motherName: '',//????
        idCard: '',//	????
        gender: '',//??
        occupancyDuration: '',//????
        education: '',//????
        numberOfChildren: '',//????
        address: '', //????
        residentialDistricts: '',
        residentialDistrics: '',
        residentialCity: '',
        residentialProvince: '',
        maritalStatus: '', //????
        socialAccounts: '', //????
        userId:localStorage.getItem('userId')
      }
    }
  },
  mounted() {
    this.load();
  },
  methods: {
    load () {
      const _this = this;
      this.$http.get('Main/GetSelection')
      .then(function (response) {
        var arr = {};
        var data = response.data.data;
        for(var i=0;i<data.length;i++){
            var value = data[i];
            if(!arr[value.selectType]){
             arr[value.selectType] = [];
            }
            arr[value.selectType].push(value);
        }
        _this.selection = arr;
        _this.userInfo();
      })
      .catch(function (error) {
        _this.failed = true
      });
    },
    userInfo () {
      const _this = this;
      this.$http.get('User/GetUserAllInfo', {params: {userId:localStorage.getItem('userId')}})
        .then(function (response) {
          var data = response.data.data;
          _this.model = data.userPersonalInfo;
        })
        .catch(function (error) {
          _this.failed = true
        });
    },
    submit () {
      this.$f7.dialog.create({
        title: 'Save information',
        text: 'Whether to save the modified information?',
        buttons: [
          {text: 'Save', onClick: this.ajax },
          {text: 'Give up'}
        ],
        verticalButtons: true
      }).open();
    },
    async ajax () {
        this.loading = true;
        const _this = this;
        await this.$http.get('User/EditUserPersonalInfo', {
          params:_this.model
        })
          .then(async function (response) {
            _this.loading = false;
            //????
            if(response.data.result != 0){
              _this.failed = true;
              _this.msg = response.data.message;
              await _this.sleep(1000);
              _this.failed = !_this.failed;
            } else {
              //????
              _this.success = true;
              await _this.sleep(1000);
              _this.success = !_this.success;
            }
          })
          .catch(function (error) {
            _this.failed = true
          });
      //this.success = !this.success;
      //await this.sleep(2000);
      //this.success = !this.success;

    },
    async sleep (ms) {
      return new Promise(resolve => setTimeout(resolve, ms))
    }
  },
}
</script>



