<template>
  <f7-page>
    <f7-navbar title="Contac information" back-link="Back"></f7-navbar>

    <div class="mt-55">
      <f7-block-title>The first contact</f7-block-title>
      <f7-list no-hairlines-md>
      <!--
        <f7-list-item>
          <f7-label>Social relationship</f7-label>
          <f7-input type="text" v-model="model1.relationShip" @input="model1.relationShip = $event.target.value" placeholder="Please enter" clear-button></f7-input>
        </f7-list-item>-->

        <li>
          <div class="item-content item-input">
            <div class="item-inner">
              <div class="item-title item-label">Social relationship</div>
              <select name="mac-windows" v-model="model1.relationShip">
                <option v-for="item in selection['relationship']" :value="item.selectValue">{{item.selectText}}</option>
              </select>
            </div>
          </div>
        </li>

<!--        <li>
          <a class="item-link smart-select smart-select-init" data-open-in="sheet" data-routable-modals="false"
             data-close-on-select="true">
            <select name="mac-windows" v-model="model1.relationShip">
              <option v-for="item in selection['relationship']" :value="item.selectValue">{{item.selectText}}</option>
            </select>
            <div class="item-content item-input">
              <div class="item-inner">
                <div class="item-title item-label">Social relationship</div>
              </div>
            </div>
          </a>
        </li>-->

        <f7-list-item>
          <f7-label>Name</f7-label>
          <f7-input type="text" v-model="model1.relationUserName" @input="model1.relationUserName = $event.target.value" placeholder="Please enter" clear-button></f7-input>
        </f7-list-item>
        <f7-list-item>
          <f7-label>Phone number</f7-label>
          <f7-input type="text" v-model="model1.phone" @input="model1.phone = $event.target.value" placeholder="Please enter" clear-button></f7-input>
        </f7-list-item>

      </f7-list>

      <f7-block-title class="mt-44">The Second contact</f7-block-title>
      <f7-list no-hairlines-md>
        <!--<li>
          <a class="item-link smart-select smart-select-init" data-open-in="sheet" data-routable-modals="false"
             data-close-on-select="true">
            <select name="mac-windows" v-model="model2.relationShip">
              <option v-for="item in selection['relationship']" :value="item.selectValue">{{item.selectText}}</option>
            </select>
            <div class="item-content item-input">
              <div class="item-inner">
                <div class="item-title item-label">Social relationship</div>
              </div>
            </div>
          </a>
        </li>-->
        <li>
          <div class="item-content item-input">
            <div class="item-inner">
              <div class="item-title item-label">Social relationship</div>
              <select name="mac-windows" v-model="model2.relationShip">
                <option v-for="item in selection['relationship']" :value="item.selectValue">{{item.selectText}}</option>
              </select>
            </div>
          </div>
        </li>
        <f7-list-item>
          <f7-label>Name</f7-label>
          <f7-input type="text" v-model="model2.relationUserName" @input="model2.relationUserName = $event.target.value" placeholder="Please enter" clear-button></f7-input>
        </f7-list-item>
        <f7-list-item>
          <f7-label>Phone number</f7-label>
          <f7-input type="text" v-model="model2.phone" @input="model2.phone = $event.target.value" placeholder="Please enter" clear-button></f7-input>
        </f7-list-item>
      </f7-list>

      <div class="sf" v-if="success || failed">
        <div class="success" v-if="success">
          <img src="../images/success.png" width="50">
          <p>Save success</p>
        </div>
        <div class="failed" v-if="failed">
          <img src="../images/failed.png" width="50">
          <p>Save success</p>
        </div>
      </div>
    </div>

    <f7-toolbar class="button-bottom">
      <a class="col button button-big button-fill" :class="{ disable: !save }" @click="submit">Save</a>
    </f7-toolbar>
  </f7-page>
</template>

<script>
//userContactInfo
export default {
  data () {
    return {
      save: true,
      success: false,
      failed: false,
      selection: {},
      model1:{
        userId: localStorage.getItem('userId')
      },
      model2:{
        userId: localStorage.getItem('userId')
      },
      phone: '',
      relationShip: '',
      relationUserName: ''
    }
  },
  mounted() {
    this.load();
  },
  methods: {
    load() {
        const _this = this;
        this.$http.get('Main/GetSelection')
          .then(function (response) {
            var arr = {};
            var data = response.data.data;
            for (var i = 0; i < data.length; i++) {
              var value = data[i];
              if (!arr[value.selectType]) {
                arr[value.selectType] = [];
              }
              arr[value.selectType].push(value);
            }
            _this.selection = arr;
            _this.userInfo();
          })
          .catch(function (error) {
            _this.failed = true
          });
      },
      userInfo () {
          const _this = this;
          this.$http.get('User/GetUserAllInfo', {params: {userId:localStorage.getItem('userId')}})
            .then(function (response) {
              var data = response.data.data;
              _this.model1 = Object.assign(data.userContactInfo[0], _this.model1);
              _this.model2 = Object.assign(data.userContactInfo[1], _this.model2);
            })
            .catch(function (error) {
              _this.failed = true
            });
        },
    submit () {
      this.$f7.dialog.create({
        title: 'Save information',
        text: 'Whether to save the modified information?',
        buttons: [
          {text: 'Save', onClick: this.ajax },
          {text: 'Give up'}
        ],
        verticalButtons: true
      }).open();
    },
    async ajax () {
      this.loading = true;
      const _this = this;
      await this.$http.get('User/EditUserContactInfo', {
        params: _this.model1
      })
        .then(async function (response) {
          _this.loading = false;
          //????
          if(response.data.result != 0){
            _this.failed = true;
            _this.msg = response.data.message;
            await _this.sleep(1000);
            _this.failed = !_this.failed;
          } else {
            //????
            //_this.success = true;
            //await _this.sleep(1000);
            //_this.success = !_this.success;
            _this.ajax2();
          }
        })
        .catch(function (error) {
          _this.failed = true
        });
    },
    async ajax2 () {
      this.loading = true;
      const _this = this;
      await this.$http.get('User/EditUserContactInfo', {
        params: _this.model2
      })
        .then(async function (response) {
          _this.loading = false;
          //????
          if(response.data.result != 0){
            _this.failed = true;
            _this.msg = response.data.message;
            await _this.sleep(1000);
            _this.failed = !_this.failed;
          } else {
            //????
            _this.success = true;
            await _this.sleep(1000);
            _this.success = !_this.success;
          }
        })
        .catch(function (error) {
          _this.failed = true
        });
    },
    async sleep (ms) {
      return new Promise(resolve => setTimeout(resolve, ms))
    }
  },
}
</script>



