<template>
  <f7-page>
    <f7-navbar title="Repayment" back-link="Back"></f7-navbar>

    <div class="repayment mt-44">
      <div class="row no-gap">
        <div class="col-20 img">
          <img src="../images/repay.png" width="40"/>
        </div>
        <div class="col-80">
          <div class="repay-header row">
            <div class="col money">Rp{{info.debitMoney}}</div>
            <div class="col days">{{info.debitPeroid}} Days</div>
            <div class="col number">No. {{info.debitId}}</div>
            <!-- yellow green gray black red-->
          </div>
          <div class="record-info">
            <p>Daily interest : Rp{{info.dayInterset}}</p>
            <p>Submission data : {{info.createTime}}</p>
          </div>
        </div>
      </div>
      <div class="repay-footer"></div>
    </div>

    <div class="payback">
      <p>Need to pay back</p>
      <h3>Rp{{info.payBackMoney}}</h3>
    </div>

    <div class="p-15 bp">
      <div class="upload-list">
        <upload v-on:uploadImage="uploadImage" :url="model.certificateUrl" />
        <p class="text-c c-666">Voucher to upload repayment</p>
        <p class="text-c c-999">Repayment time has already been made by the bank</p>
      </div>
      <div class="upload-list" v-for="n in uploads">
        <upload />
      </div>
    </div>

<!--
    <button class="button button-big more-photos mb-20" @click="uploads++"><i class="fa fa-plus" aria-hidden="true"></i> More photos</button>
-->
    <f7-toolbar class="button-bottom">
      <a class="col button button-big button-fill" :class="{ disable: !save }" @click="submit">Save</a>
    </f7-toolbar>

    <div class="sf" v-if="success || failed">
      <div class="success" v-if="success">
        <img src="../images/success.png" width="50">
        <p>Save success</p>
      </div>
      <div class="failed" v-if="failed">
        <img src="../images/failed.png" width="50">
        <p>{{msg ? msg : 'Save failed'}}</p>
      </div>
    </div>


  </f7-page>
</template>

<script>
import upload from '@/components/upload.vue'

export default {
  data () {
    return {
      save: true,
      uploads: 0,
      success: false,
      failed: false,
      msg: '',
      info: {},
      model:{
        userId: localStorage.getItem('userId'),
        debitId: this.$f7Route.params.debitId,
        payBackDebitMoney: 0,
        certificateUrl: ''
      }
    }
  },
  mounted() {
   // this.model.debitId = this.$f7Route.params.debitId;
    this.load();
  },
  components: {
    upload
  },
  methods: {
    async load () {
      const _this = this;
      await this.$http.get('Debit/GetUserDebitRecord', {
        params: {debitId: _this.model.debitId}
      })
        .then(async function (response) {
          _this.loading = false;
          if(response.data.result != 0){
            _this.failed = true;
            _this.msg = response.data.message;
            await _this.sleep(1000);
            _this.failed = !_this.failed;
          } else {
            _this.info = response.data.data;
          }
        })
        .catch(function (error) {
          _this.failed = true
        });
    },
    uploadImage(url) {
      this.model['certificateUrl'] = url;
    },
    async submit () {
       const _this = this;
       await this.$http.get('Debit/SubmitPayBackDebitRequest', {
         params: _this.model
       })
     .then(async function (response) {
       _this.loading = false;
       if(response.data.result != 0){
         _this.failed = true;
         _this.msg = response.data.message;
         await _this.sleep(1000);
         _this.failed = !_this.failed;
       } else {
           _this.success = true;
           await _this.sleep(1000);
           _this.success = !_this.success;
           _this.$f7.router.navigate("/record");
       }
     })
     .catch(function (error) {
       console.log(error)
       _this.failed = true
     });
    },
    async sleep(ms) {
      return new Promise(resolve => setTimeout(resolve, ms))
    }
  }
}
</script>



