<template>
  <f7-page>
    <f7-navbar title="Loan information" back-link="Back"></f7-navbar>

    <div class="block">
      <div class="pt-20">
        <p class="c-999">Data integrity : {{allPercent}}%</p>
        <p><span :data-progress="allPercent" class="progressbar" id="progressbar"></span></p>
      </div>

      <div class="pt-20 information">
        <a href="/personal" class="button">
          <span class="float-left">Personal information</span>
          <img src="../images/arrow.png">
          <span class="float-right">{{personal}}%</span>
        </a>
        <a href="/work" class="button">
          <span class="float-left">Working information</span>
          <img src="../images/arrow.png">
          <span class="float-right">{{working}}%</span>
        </a>
        <a href="/contact" class="button">
          <span class="float-left">Contact information</span>
          <img src="../images/arrow.png">
          <span class="float-right">{{contact}}%</span>
        </a>
        <a href="/photo" class="button">
          <span class="float-left">Upload photo</span>
          <img src="../images/arrow.png">
          <span class="float-right">{{upload}}%</span>
        </a>
      </div>

      <a class="col button button-big button-fill" :class="{ disable: allPercent < 100 }" :href="url">Next</a>
    </div>
  </f7-page>
</template>

<script>
export default {
  data () {
    return {
      personal: 0,
      working: 0,
      contact: 0,
      upload: 0,
      currProduct: {},
      allPercent: 0
    }
  },
  mounted() {
    this.init();
  },
  methods: {
    init() {
      var currProduct = localStorage.getItem('currProduct');
      this.currProduct = JSON.parse(currProduct);
      const _this = this;
      this.$http.get('User/GetUserAllInfo', {params: {userId:localStorage.getItem('userId')}})
        .then(function (response) {
           _this.contact = response.data.data.contactPercent
           _this.working = response.data.data.workingPercent
           _this.personal = response.data.data.personalPercent
           _this.upload = response.data.data.cardPercent
           _this.allPercent = response.data.data.allPercent

           _this.$f7.progressbar.set('#progressbar', _this.allPercent)
        })
        .catch(function (error) {
        });
    },
  },
  computed: {
    //integrity () {
      //return (this.personal + this.working + this.contact + this.upload) / 4;
    //},
    url () {
      if(this.allPercent == 100) {
        return '/info-comfirm'
      }
      return '#';
    }
  }
}
</script>



