<template>
  <f7-page>
    <tabbar active="record"/>
    <f7-navbar title="Record"></f7-navbar>

    <div class="network" v-if="!network && !loading">
      <img src="../images/network.png" width="60"/>
      <h3>Network connection failure</h3>
      <p>Please check whether the network is connected</p>
    </div>

    <div class="record" v-if="network && !loading">
      <list v-for="record in records" :key="record.id" :record="record"/>
    </div>
  </f7-page>
</template>

<script>
import tabbar from '@/components/tabbar.vue';
import list from '@/components/list.vue';

export default {
  data () {
    return {
      loading: false,
      network: true,
      records: []
    }
  },
  components: {
    tabbar,
    list
  },
  methods: {
    async load () {
      this.loading = true;

      const vue = this;
      await this.$http.get('Debit/GetUserDebitRecords?userId=' + localStorage.getItem('userId'))
        .then(function (response) {
          vue.records = response.data.data;
          vue.loading = false;
        })
        .catch(function (error) {
          this.error = true
        });
    }
  },
  mounted () {
    this.load();
  },
  watch: {
    loading (val) {
      if(val) {
        this.$f7.dialog.preloader();
      } else {
        this.$f7.dialog.close();
      }
    }
  }
}
</script>



