<template>
  <f7-page>
    <form class='hw-overlay login'>
    	<div class="hw-layer-wrap">
        <p class="error" v-if="message">{{message}}</p>
        <div class="input">
          <img src="../images/phone.png" width="15"/>
          <input type="text" placeholder="Phone number" v-model="phone">
        </div>
        <div class="input">
          <img src="../images/password.png" width="15"/>
          <input type="password" placeholder="Password" v-model="password">
        </div>
        <p class="forget">forget password? <a href="/password/set">click here</a></p>
        <a class="col button button-big button-fill" @click="submit">Sign IN</a>
        <p class="reg"><a href="/password/sign">New to pinjamcepat? Sign Up new !</a></p>
    	</div>
    </form>
  </f7-page>
</template>

<script>
export default {
  data () {
    return {
      message: '',
      phone: '',
      password: '',
    }
  },
  methods: {
    submit () {
      var _this = this;
      this.$http.get('/User/UserLogin?phone='+this.phone+'&password='+this.password)
        .then(function (response) {
            if(response.data.errorCode != 0){
              _this.message = response.data.message;
            } else{
              localStorage.setItem('token', response.data.data.token);
              localStorage.setItem('userId', response.data.data.userId);
              localStorage.setItem('userName', response.data.data.userName);
              _this.$f7.router.navigate("/home");
            }
        })
        .catch(function (error) {
          _this.message = 'request error';
        });

    }
  },
}
</script>



