<template>
  <div class="card record-list">
    <div class="row no-gap">
      <div class="col-20 number">
        <span>No.</span>
        <span>{{record.debitId}}</span>
      </div>
      <div class="col-80">
        <div class="record-header row">
          <div class="col-25 money">Rp{{record.debitMoney}}</div>
          <div class="col-25 days">{{record.debitPeroid}} Days</div>
          <div class="col-50 status" :class="status">{{statusEnum[record.status]}}</div>
          <!-- yellow green gray black red-->
        </div>
        <div class="record-info">
          <p>Daily interest : Rp{{record.dayInterset}}</p>
          <p>Submission data : {{ record.createTime }}</p>
        </div>
      </div>
      <!-- default/red -->
      <div class="col-100 record-remark" :class="remarkStatus" v-if="remark">
        {{ record.description }}
      </div>
    </div>
    <a class="record-footer" :href="'/repayment/' + record.debitId" v-if="repayment">Repayment</a>
  </div>
</template>

<script>
export default {
  props: ['record'],
  data () {
    return {
      color: ['yellow', 'green', 'gray', 'black', 'red'],
      statusEnum:{
        "-2" : "",
        "-1":"",
        "0":"",
        "1":"",
        "2":"",
        "3" : "",
        "4" : "",
        "5" : ""
      }
    }
  },
  //
  computed: {
    status () {
      return this.color[Math.floor(Math.random()*this.color.length)]
    },
    remarkStatus () {
      const color = ['default', 'red'];
      return color[Math.floor(Math.random()*color.length)]
    },
    remark () {
      return Math.random() > 0.5
    },
    repayment () {
      return this.record.status == 1 || this.record.status == -2;
//      return Math.random() > 0.5
    }
  }
}
</script>



