<template>
  <f7-page>
    <div class="navbar header">
      <div class="navbar-inner sliding navbar-current">
        <div class="left"></div>
        <div class="title">
          <h3>Rp1,000,000 loans</h3>
          <p>have been provided for 1,000 people</p>
        </div>
        <div class="right"></div>
      </div>
    </div>

    <tabbar active="home"/>

    <div class="loan">
      <div class="block-header"><i class="icon icon-money"></i> Select the loan amount</div>
      <div class="block">
        <div class="row">
          <div class="col" v-for="item in debitStyle">
            <label class="radio">
              <input type="radio" name="debitMoney" v-model="debitMoney" :value="item">
              <div class="button">Rp {{item}}</div>
            </label>
          </div>
        </div>
      </div>

      <div class="block-header"><i class="icon icon-time"></i> Select the loan term</div>
      <div class="block">
        <div class="row">
          <div class="col" v-for="item in debitPeriods">
            <label class="radio">
              <input type="radio" name="debitPeriod" v-model="debitPeriod" :value="item">
              <div class="button">{{item}} days</div>
            </label>
          </div>
        </div>
      </div>
    </div>

    <div class="daily">
      <div class="block">
        <div class="row no-gap">
          <div class="col-50 pl-20">
            <span>Daily interest</span>
            <p>Rp {{dailyInterest}}</p>
          </div>
          <div class="col-50 pl-20">
            <span>Amount to payback</span>
            <p>Rp {{payBackMoney}}</p>
          </div>
          <div class="col-80 pt-20 center">
            <button class="col button button-big button-fill" :class="{ disable: !disable }" @click="borrow">Borrow Now</button>
          </div>
        </div>
      </div>
    </div>
  </f7-page>
</template>

<script>
  import tabbar from '@/components/tabbar.vue';
  import {mapActions} from 'vuex'

  export default {
    components: {
      tabbar
    },
    data() {
      return {
        error: false,
        debitStyle:[],
        debitMoney: '',
        debitPeriod: '',
        debitPeriods: [],
        dailyInterest: '',
        payBackMoney: ''
      }
    },
    mounted() {
      this.load();
    },
    methods: {
      borrow () {
        if(this.debitPeriod != '' || this.debitMoney != '') {
          this.$f7.router.navigate("/loan-info");
        }
      },
      async load () {
        var _this = this;

        const vue = this;
        await this.$http.get('Main/GetInitDebitStyle')
          .then(function (response) {
            _this.debitStyle = response.data.data.debitStyle;
            _this.debitPeriods = response.data.data.debitPeriod;
            _this.debitMoney = _this.debitStyle[0];
            _this.debitPeriod = _this.debitPeriods[0];
          })
          .catch(function (error) {
            _this.error = true
          });
      },
      calcInterestRate() {
        if(!this.debitMoney || !this.debitPeriod){
          return;
        }
        var _this = this;
        var url = '/Debit/CalcInterestRate?debitMoney='+this.debitMoney+'&debitPeriod='+this.debitPeriod;
        this.$http.get(url)
          .then(function (response) {
            if (response.data.errorCode != 0) {
              _this.message = response.data.message;
            } else {
              _this.dailyInterest = response.data.data.dailyInterest;
              _this.payBackMoney = response.data.data.payBackMoney;
              var currProduct = {
                debitMoney: _this.debitMoney,
                debitPeriod: _this.debitPeriod,
                payBackMoney: _this.payBackMoney
              };
              localStorage.setItem('currProduct', JSON.stringify(currProduct));
            }
          })
          .catch(function (error) {
            _this.message = 'request error';
          });

      }
    },
    computed: {
      disable () {
        if(this.debitPeriod && this.debitMoney) {
          return true;
        }

        return false;
      }
    },
    watch: {
      debitMoney() {
        this.calcInterestRate();
      },
      debitPeriod() {
        this.calcInterestRate();
      }

    }
  }


</script>



