<template>
  <f7-page>
    <f7-navbar title="Upload photo" back-link="Back"></f7-navbar>

    <div class="mt-55 p-15 bp">
      <div class="upload-list">
        <upload v-on:uploadImage="uploadImage" :url="model.IdCardUrl"/>
        <p class="text-c c-666">ID Card front page</p>
      </div>
      <div class="upload-list">
        <upload v-on:uploadImage="uploadImage2" :url="model.WorkinfoCardUrl"/>
        <p class="text-c c-666">Working ID Card</p>
        <p class="text-c c-999">if you don't have, you can take a phont wearing company uniform</p>
      </div>
      <!--<div class="upload-list" v-for="n in uploads">
        <upload v-on:uploadImage="uploadImage" />
      </div>-->

      <!--<button class="button button-big more-photos mb-20" @click="addUpload"><i class="fa fa-plus" aria-hidden="true"></i> More photos</button>-->

      <div class="sf" v-if="success || failed">
        <div class="success" v-if="success">
          <img src="../images/success.png" width="50">
          <p>Save success</p>
        </div>
        <div class="failed" v-if="failed">
          <img src="../images/failed.png" width="50">
          <p>Save failed</p>
        </div>
      </div>
    </div>


    <f7-toolbar class="button-bottom">
      <a class="col button button-big button-fill" :class="{ disable: !save }" @click="submit">Save</a>
    </f7-toolbar>
  </f7-page>
</template>

<script>
  import upload from '@/components/upload.vue'

  export default {
    data() {
      return {
        save: true,
        success: false,
        failed: false,
        uploads: 0,
        model1: {
          userId: localStorage.getItem('userId'),
          type: 1
        },
        model2: {
          userId: localStorage.getItem('userId'),
          type: 2
        },
        model: {
          IdCardUrl:"",
          WorkinfoCardUrl:""
        }
      }
    },
    components: {
      upload
    },
    mounted() {
      this.userInfo();
    },
    methods: {
      addUpload() {
        this.uploads++;
      },
      uploadImage(url) {
        this.model1['url'] = url;
      },
      uploadImage2(url) {
        this.model2['url'] = url;
      },
      userInfo() {
        const _this = this;
        this.$http.get('User/GetUserAllInfo', {params: {userId: localStorage.getItem('userId')}})
          .then(function (response) {
            var data = response.data.data;
            _this.model = data.userCards;
//            console.log(_this.model)
            if(_this.model.IdCardUrl){
              _this.model1['url'] = _this.model.IdCardUrl;
            }
            if(_this.model.WorkinfoCardUrl){
              _this.model2['url'] = _this.model.WorkinfoCardUrl;
            }
          })
          .catch(function (error) {
            console.log(error)
            _this.failed = true
          });
      },
      submit() {
        this.$f7.dialog.create({
          title: 'Save information',
          text: 'Whether to save the modified information?',
          buttons: [
            {text: 'Save', onClick: this.ajax},
            {text: 'Give up'}
          ],
          verticalButtons: true
        }).open();
      },
      async ajax() {
        this.loading = true;
        const _this = this;
        await this.$http.get('User/EditUserPhotos', {
          params: _this.model1
        })
          .then(async function (response) {
            _this.loading = false;
            if (response.data.result != 0) {
              _this.failed = true;
              _this.msg = response.data.message;
              await _this.sleep(1000);
              _this.failed = !_this.failed;
            } else {
              _this.ajax1();
            }
          })
          .catch(function (error) {
            _this.failed = true
          });
      },
      async ajax1() {
        this.loading = true;
        const _this = this;
        await this.$http.get('User/EditUserPhotos', {
          params: _this.model2
        })
          .then(async function (response) {
            _this.loading = false;
            if (response.data.result != 0) {
              _this.failed = true;
              _this.msg = response.data.message;
              await _this.sleep(1000);
              _this.failed = !_this.failed;
            } else {
              _this.success = true;
              await _this.sleep(1000);
              _this.success = !_this.success;
            }
          })
          .catch(function (error) {
            _this.failed = true
          });
      },
      async sleep(ms) {
        return new Promise(resolve => setTimeout(resolve, ms))
      }
    }
  }
</script>



