<template>
  <f7-page>
    <f7-navbar title="Information confirmation" back-link="Back"></f7-navbar>

    <div class="confirmation-info">
      <f7-row no-gap>
        <f7-col width="40">
          <f7-col width="100">
            <span>Loan amount</span>
            <p>Rp {{currProduct.debitMoney}}</p>
          </f7-col>
          <f7-col width="100">
            <span>Due date</span>
            <p>{{currProduct.debitPeriod}} Days</p>
          </f7-col>
        </f7-col>
        <f7-col width="60">
          <span>Amount to payback</span>
          <p class="primary">Rp{{currProduct.payBackMoney}}</p>
        </f7-col>
      </f7-row>
    </div>

    <div class="receivable">
      <div class="title">
        <i class="fa fa-credit-card" aria-hidden="true"></i> Loan receivable bank
      </div>
      <f7-list no-hairlines-md>
        <f7-list-item>
          <f7-label>Bank of deposit</f7-label>
          <f7-input type="text" v-model="bankInfo.bankName" @input="bankInfo.bankName = $event.target.value" placeholder="Please enter" clear-button></f7-input>
        </f7-list-item>

        <f7-list-item>
          <f7-label>Bank account</f7-label>
          <f7-input type="text" v-model="bankInfo.bankCode" @input="bankInfo.bankCode = $event.target.value" placeholder="Please enter" clear-button></f7-input>
        </f7-list-item>

        <f7-list-item>
          <f7-label>Account name</f7-label>
          <f7-input type="text" v-model="bankInfo.contactName" @input="bankInfo.contactName = $event.target.value" placeholder="Please enter" clear-button></f7-input>
        </f7-list-item>

        <f7-list-item>
          <f7-label>Loan purpose</f7-label>
          <f7-input type="text" v-model="description" @input="description = $event.target.value" placeholder="Please enter" clear-button></f7-input>
        </f7-list-item>
      </f7-list>
    </div>

    <div class="agree">
      <div class="content">
        <div class="checkbox checkbox-primary pb-20">
          <input type="checkbox" id="agree" v-model="agree">
          <label for="agree">
            I agree with the <a href="">loan agreement</a>
          </label>
        </div>

        <a class="col button button-big button-fill" :class="{ disable: !agree }" @click="submit">Submit applications</a>
      </div>
    </div>

    <div class="hw-overlay" v-if="submitted">
    	<div class="hw-layer-wrap">
        <img src="../images/submit-pic.png" width="60">
        <p>Loan information has been submitted</p>
        <span>We will complete the review as soon as possible. Please keep the mobile phone open.</span>
        <button class="col button button-big button-fill mt-20" @click="ok">Ok</button>
    	</div>
    </div>

    <div class="sf" v-if="success || failed">
      <div class="success" v-if="success">
        <img src="../images/success.png" width="50">
        <p>Save success</p>
      </div>
      <div class="failed" v-if="failed">
        <img src="../images/failed.png" width="50">
        <p>{{msg ? msg : 'Save failed'}}</p>
      </div>
    </div>

  </f7-page>
</template>

<script>
export default {
  data () {
    return {
      agree: true,
      submitted: false,
      success: false,
      failed: false,
      msg: '',
      bankInfo:{
        bankId: '',
        bankName:'',
        subBankName: '',
        bankCode: '',
        contact: '',
        contactName: ''
      },
      description: '',
      currProduct:{}
    }
  },
  mounted() {
      this.load();
      this.init();
    },
  methods: {
     init () {
        var currProduct = localStorage.getItem('currProduct');
        this.currProduct = JSON.parse(currProduct);
     },
     load () {
      const _this = this;
      this.$http.get('User/GetUserBankInfo', {params: {userId:localStorage.getItem('userId')}})
      .then(function (response) {
        _this.bankInfo = response.data.data;
      })
      .catch(function (error) {
        _this.failed = true
      });
    },
    async ajax() {
        this.loading = true;
        const _this = this;
        _this.bankInfo['userId'] = localStorage.getItem('userId');
        this.$http.get('User/SaveUserBankInfo', {
          params: _this.bankInfo
        })
          .then(async function (response) {
            _this.loading = false;
            //????
            if(response.data.result != 0){
              _this.failed = true;
              _this.msg = response.data.message;
              await _this.sleep(1000);
              _this.failed = !_this.failed;
            } else {
              if(!_this.bankInfo.bankId){
                _this.bankInfo.bankId = response.data.bankId;
              }
              _this.debitSubmit();
            }
          })
          .catch(function (error) {
            console.log(error)
            _this.failed = true
          });
      },
      debitSubmit () {
        const _this = this;
        var obj = {
            description: this.description,
            userId: localStorage.getItem('userId'),
            debitMoney: this.currProduct.debitMoney,
            debitPeriod: this.currProduct.debitPeriod,
            bankId: this.bankInfo.bankId
        }
        this.loading = true;
        this.$http.get('Debit/SubmitDebitRequest', {
          params: obj
        })
          .then(async function (response) {
            _this.loading = false;
            //????
            if(response.data.result != 0){
              _this.failed = true;
              _this.msg = response.data.message;
              await _this.sleep(2000);
              _this.failed = !_this.failed;
            } else {
              //????
              _this.success = true;
              await _this.sleep(1000);
              _this.success = !_this.success;
              _this.$f7.router.navigate("/record");
            }
          })
          .catch(function (error) {
            _this.failed = true
            console.log(error)
          });

      },
    submit () {
      if (!this.agree)
        return;
      this.submitted = !this.submitted;
    },
    ok () {
      this.submitted = !this.submitted;
      this.ajax();
    },
    async sleep(ms) {
      return new Promise(resolve => setTimeout(resolve, ms))
    }
  }
}
</script>



