<template>
  <f7-page class="top">
    <tabbar active="mine"/>
    <f7-navbar title="Mine"></f7-navbar>

    <div class="list">
      <ul>
        <li>
          <div class="item-content">
            <div class="item-media"><img src="../images/portrait.png" width="20"/></div>
            <div class="item-inner">
              <div class="item-title">{{userName}}</div>
            </div>
          </div>
        </li>
      </ul>
    </div>

    <div class="list links-list pt-20">
      <ul>
        <li><a href="/contactus">Contact us</a></li>
        <li><a href="#" @click="about = !about">About</a></li>
        <li><a href="/help">Help</a></li>
      </ul>
    </div>

    <div class="row">
      <div class="col-90 pt-50 center">
        <a class="col button button-big logout" href="#" @click="signout">Sign out</a>
      </div>
    </div>

    <div class="hw-overlay" v-if="about" @click="about = !about">
    	<div class="hw-layer-wrap about">
        <div class="about-header">
          <img src="../images/logo-color.png" width="60">
          <p class="title">PINJAMCEPAT</p>
          <p class="version">V1.0</p>
        </div>
        <div class="about-list">
          <ul>
            <li> It is an online financial services product designed to solve the financing difficulties of small and micro businesses. Customers complete the application through the online operation of the whole process, usually after the submission of the main information and actual operating data, the loan application assessment can be carried out. Based on the rapid review of large data technology, the customer's experience of borrowing and repayment has been greatly enhanced.</li>
          </ul>
        </div>
    	</div>
    </div>

  </f7-page>
</template>

<script>
import tabbar from '@/components/tabbar.vue';

export default {
  data () {
    return {
      about: false,
      userName:'',
    }
  },
  mounted() {
    this.userName = localStorage.getItem('userName');
  },
  methods: {
    signout(){
      localStorage.clear();
      //
      this.$f7.router.navigate("/");
    }
  },
  components: {
    tabbar
  }
}
</script>



