<template>
  <f7-page>
    <f7-navbar :title="title" back-link="Back"></f7-navbar>

    <div class="mt-44 pt-10">
      <p class="code" v-if="sended">The Verification code has been sent to {{phone}}</p>

      <div class="password">

        <div class="input">
          <img src="../images/phone.png" width="15"/>
          <input type="text" placeholder="Phone Number" v-model="phone">
        </div>

        <div class="input row">
          <div class="col-80 br">
            <img src="../images/code.png" width="15"/>
            <input type="text" placeholder="Verification Code" v-model="code" class="input-code">
          </div>
          <div class="col-20">
            <span class="send resend" v-if="sendMsgDisabled">{{time}} s</span>
            <span class="send" v-if="!sendMsgDisabled" @click="send">Resend</span>
          </div>
        </div>

        <div class="input">
          <img src="../images/password.png" width="15"/>
          <input type="password" placeholder="Password" v-model="password">
        </div>

        <div class="row">
          <div class="col-90 pt-50 center">
            <a href="#" class="col button button-big button-fill" :class="{ disable: !disable }" @click="register">{{btnText}}</a>
          </div>
        </div>
        <p class="error" v-if="error">Verification code error</p>
      </div>
    </div>

    <div class="sf" v-if="success || failed">
      <div class="success" v-if="success">
        <img src="../images/success.png" width="50">
        <p>Save success</p>
      </div>
      <div class="failed" v-if="failed">
        <img src="../images/failed.png" width="50">
        <p>{{msg ? msg : 'Save failed'}}</p>
      </div>
    </div>

  </f7-page>
</template>

<script>
  export default {
    data() {
      return {
        success: false,
        failed: false,
        sendMsgDisabled: false,
        time: 60,
        error: false,
        sended: false,
        phone: '',
        password: '',
        code: '',
        btnText: 'Sign Up'
      }
    },
    mounted() {
    },
    computed: {
      title() {
        if (this.$f7Route.params.status == 'set') {
          this.btnText = 'Submit';
          return 'Set the password';
        }
        return 'Sign Up';
      },
      disable () {
        if(this.code) {
          return true;
        }
        return false;
      }
    },
    methods: {
      async register() {
        const _this = this;
        await this.$http.get('User/UserRegister', {
          params: {
            phone: _this.phone,
            code: _this.code,
            password: _this.password
          }
        })
          .then(async function (response) {
            if (response.data.result != 0) {
              _this.error = response.data.message;
            } else {
              _this.success = true;
              await _this.sleep(1000);
              _this.success = !_this.success;
              _this.$f7.router.navigate("/login");
            }
          })
          .catch(function (error) {
            _this.failed = true
          });
      },
      async send() {
        const _this = this;
        if (!_this.phone) {
          return;
        }
        await this.$http.get('User/GetVerificateCode', {
          params: {phone: _this.phone}
        })
          .then(async function (response) {
            if (response.data.result != 0) {
              _this.error = response.data.message;
            } else {
              _this.interval();
            }
          })
          .catch(function (error) {
            _this.failed = true
          });
      },
      interval() {
        let me = this;
        me.sendMsgDisabled = true;
        let interval = window.setInterval(function () {
          if ((me.time--) <= 0) {
            me.time = 60;
            me.sendMsgDisabled = false;
            window.clearInterval(interval);
          }
        }, 1000);
      },
      async sleep(ms) {
        return new Promise(resolve => setTimeout(resolve, ms))
      }

    }
  }
</script>



