import HomePage from '@/pages/home.vue';
import LoanInfoPage from '@/pages/loan-info.vue';
import InfoConfirmPage from '@/pages/info-confirm.vue';
import PersonalPage from '@/pages/personal.vue';
import WorkPage from '@/pages/work.vue';
import ContactPage from '@/pages/contact.vue';
import PhotoPage from '@/pages/photo.vue';
import RecordPage from '@/pages/record.vue';
import RepaymentPage from '@/pages/repayment.vue';
import MinePage from '@/pages/mine.vue';
import HelpPage from '@/pages/help.vue';
import PasswordPage from '@/pages/password.vue';
import LoginPage from '@/pages/login.vue';
import ContactusPage from '@/pages/contactus.vue'

import NotFoundPage from '@/pages/not-found.vue';

export default [
  {
    path: '/',
    // component: HomePage,
    redirect: function (route, resolve, reject) {
      if (!localStorage.getItem('token')) {
        resolve('/login');
      } else {
        resolve('/home');
      }
    }
  },
  {
    path: '/login',
    component: LoginPage,
  },
  {
    path: '/home',
    component: HomePage,
  },
  {
    path: '/loan-info',
    component: LoanInfoPage
  },
  {
    path: '/info-comfirm',
    component: InfoConfirmPage,

  },
  {
    path: '/personal',
    component: PersonalPage,

  },
  {
    path: '/repayment/:debitId',
    component: RepaymentPage,

  },
  {
    path: '/work',
    component: WorkPage,

  },
  {
    path: '/record',
    component: RecordPage,

  },
  {
    path: '/contact',
    component: ContactPage,

  },
  {
    path: '/photo',
    component: PhotoPage,

  },
  {
    path: '/mine',
    component: MinePage,

  },
  {
    path: '/help',
    component: HelpPage,
  },
  {
    path: '/contactus',
    component: ContactusPage,
  },
  {
    path: '/password/:status',
    component: PasswordPage,

  },
  {
    path: '(.*)',
    component: NotFoundPage,
  },
];



